/*  <<<---Пример использования Media-mixins--->>>

.header {
  width:100px;

  @include media-tablet {
    width: 500px;
  }
}

*/
html {
  box-sizing: border-box;
  min-height: 100vh;
  min-width: 375px;
}

body {
  font-family: "Proxima Nova", "Arial", "Helvetica", sans-serif;
  font-size: 16px;
  min-height: 100vh;
  min-width: 375px;
  overflow: overlay;
}
body::-webkit-scrollbar {
  background: transparent;
  width: 8px;
  background-position: 100% 0;
  background-color: #f2f3f4;
  border-radius: 2px;
}
body::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background-color: #1e2039;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

img {
  max-width: 100%;
  height: auto;
}

a {
  text-decoration: none;
}

.visually-hidden,
.visually-hidden:not(:focus):not(:active),
input[type=checkbox].visually-hidden,
input[type=radio].visually-hidden {
  position: absolute;
  overflow: hidden;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  white-space: nowrap;
  border: 0;
  clip-path: inset(100%);
}

.page {
  display: grid;
  min-height: 100vh;
  background-image: url("../img/bg.png");
  background-repeat: no-repeat repeat;
  background-position: 70% 0;
  background-size: 1923px auto;
  grid-template-rows: min-content auto min-content;
}
.page--login .page-header {
  background-color: rgba(52, 84, 122, 0.15);
}
.page--login .page-content {
  background-color: rgba(52, 84, 122, 0.15);
}
.page--inner .page-header {
  background-color: rgba(255, 255, 255, 0.75);
}
.page--inner .page-content {
  background-color: rgba(255, 255, 255, 0.75);
}
.page--not-found .page-header {
  background-color: rgba(255, 255, 255, 0.75);
}
.page--not-found .page-content {
  background-color: rgba(255, 255, 255, 0.75);
}

.custom-text-input {
  width: 100%;
  color: #373d40;
  background-color: #ffffff;
  box-shadow: 0 5px 5px rgba(0, 57, 104, 0.2);
  border-radius: 5px;
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
  padding-left: 20px;
  padding-right: 20px;
  padding-top: 17px;
  padding-bottom: 16px;
  display: block;
  box-sizing: border-box;
  border: 1px solid transparent;
}
.custom-text-input:hover {
  border: 1px solid rgba(52, 84, 122, 0.3);
}
.custom-text-input:focus {
  border: 1px solid #34547a;
  outline: none;
}
.custom-text-input--invalid {
  border: 1px solid #af1c1c;
}
.custom-text-input::placeholder {
  color: #5f5d5d;
}

.custom-textarea {
  width: 100%;
  color: #373d40;
  background-color: #ffffff;
  box-shadow: 0 5px 5px rgba(0, 57, 104, 0.2);
  border-radius: 5px;
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
  padding-left: 20px;
  padding-right: 20px;
  padding-top: 17px;
  padding-bottom: 16px;
  display: block;
  box-sizing: border-box;
  border: 1px solid transparent;
  height: 100%;
}
.custom-textarea:hover {
  border: 1px solid rgba(52, 84, 122, 0.3);
}
.custom-textarea:focus {
  border: 1px solid #34547a;
  outline: none;
}
.custom-textarea--invalid {
  border: 1px solid #af1c1c;
}
.custom-textarea::placeholder {
  color: #5f5d5d;
}

.btn {
  font-weight: 700;
  font-size: 14px;
  line-height: 17px;
  text-align: center;
  text-transform: uppercase;
  color: #ffffff;
  border-radius: 5px;
  background-color: #34547a;
  border: none;
  padding-left: 40px;
  padding-right: 40px;
  padding-top: 12px;
  padding-bottom: 12px;
  cursor: pointer;
  display: inline-block;
}
.btn:hover, .btn:focus {
  box-shadow: 0 5px 4px rgba(0, 0, 0, 0.25);
}
.btn:active {
  box-shadow: inset 0 4px 4px rgba(0, 0, 0, 0.25);
}
.btn:disabled, .btn--disabled {
  background: #8290a0;
  cursor: not-allowed;
}

.page-header__wrapper {
  display: flex;
  align-items: flex-start;
}

.page-header__logo:hover, .page-header__logo:focus {
  opacity: 0.7;
}
.page-header__logo:active {
  opacity: 0.5;
}

.page-header__main-nav {
  position: relative;
  z-index: 1;
  display: flex;
  align-items: center;
  flex-grow: 1;
  justify-content: space-between;
  padding-top: 13px;
  padding-right: 16px;
  padding-bottom: 13px;
  padding-left: 50px;
  background-color: #ffffff;
  filter: drop-shadow(0 10px 10px rgba(0, 0, 0, 0.4));
}
.page-header__main-nav::after {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 100%;
  display: block;
  width: 100px;
  content: "";
  background-image: url("../img/whit-triangle.svg");
  background-repeat: no-repeat;
  background-position: 0 0;
  background-size: auto 100%;
}

.page-header__nav-toggler {
  display: none;
}

.page-header__nav-list {
  display: flex;
  flex-wrap: wrap;
  background-color: #ffffff;
}

.page-header__nav-item {
  position: relative;
  display: block;
  padding-top: 25px;
  padding-right: 25px;
  padding-bottom: 25px;
  padding-left: 25px;
  text-align: center;
  letter-spacing: 0.1em;
  color: #004578;
  background-color: #ffffff;
  font-size: 16px;
  font-weight: 700;
  line-height: 16px;
}
.page-header__nav-item--current::before {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  display: block;
  height: 5px;
  content: "";
  background: rgba(52, 84, 122, 0.5);
}
.page-header__nav-item:hover, .page-header__nav-item:focus {
  color: #ffffff;
  background: rgba(52, 84, 122, 0.5);
}
.page-header__nav-item:hover::before, .page-header__nav-item:focus::before {
  background-color: #004578;
}
.page-header__nav-item:active {
  opacity: 0.5;
}

.page-header__phone {
  display: flex;
  align-items: center;
  color: #003968;
  font-size: 16px;
  font-weight: 700;
  line-height: 19px;
}
.page-header__phone:hover, .page-header__phone:focus {
  opacity: 0.5;
}

.page-header__phone-icon {
  flex-shrink: 0;
  width: 20px;
  height: 20px;
  margin-right: 10px;
}

.page-header__user-nav {
  display: flex;
  align-items: center;
  padding-top: 21px;
  padding-right: 20px;
  padding-bottom: 15px;
  padding-left: 10%;
  color: #e3e3e4;
  background: #1e2039;
}

.page-header__user-nav-list {
  display: flex;
  align-items: center;
  column-gap: 15px;
}
.page-header__user-nav-list::before {
  display: block;
  width: 28px;
  height: 28px;
  content: "";
  background-image: url("../img/icon-user.svg");
  background-repeat: no-repeat;
  background-position: 50% 50%;
}

.page-header__user-nav-item {
  display: block;
  color: #e3e3e4;
  font-size: 16px;
  font-weight: 700;
  line-height: 16px;
}
.page-header__user-nav-item:hover, .page-header__user-nav-item:focus {
  text-decoration: underline;
}
.page-header__user-nav-item--logout:hover, .page-header__user-nav-item--logout:focus {
  opacity: 0.7;
}

@media (max-width: 1219px) {
  .page-header__main-nav {
    padding-left: 20px;
  }
  .page-header__nav-item {
    padding-right: 15px;
    padding-left: 15px;
  }
  .page-header__phone {
    display: none;
  }
}
@media (max-width: 767px) {
  .page-header--closed-nav .page-header__nav-toggler {
    background-image: url("../img/icon-menu.png");
  }
  .page-header--closed-nav .page-header__nav-list {
    display: none;
  }
  .page-header__wrapper {
    position: relative;
    z-index: 1;
    display: block;
  }
  .page-header__nav-toggler {
    display: block;
    width: 40px;
    height: 40px;
    margin: 0;
    padding: 0;
    cursor: pointer;
    border: none;
    background-color: transparent;
    background-image: url("../img/icons-close-big.png");
    background-repeat: no-repeat;
    background-position: 50% 50%;
  }
  .page-header__nav-toggler:hover, .page-header__nav-toggler:focus {
    opacity: 0.7;
  }
  .page-header__nav-toggler:active {
    opacity: 0.3;
  }
  .page-header__main-nav {
    position: static;
    display: flex;
    padding-right: 30px;
    padding-left: 30px;
    background-color: #ffffff;
    box-shadow: 0 10px 10px rgba(0, 0, 0, 0.4);
    filter: none;
  }
  .page-header__main-nav::after {
    display: none;
  }
  .page-header__nav-list {
    position: absolute;
    top: 100%;
    right: 0;
    left: 0;
    display: block;
    box-shadow: 0 10px 10px rgba(0, 0, 0, 0.4);
  }
  .page-header__nav-item {
    text-align: left;
  }
  .page-header__phone {
    display: block;
  }
  .page-header__user-nav {
    display: flex;
    justify-content: space-between;
    padding-right: 30px;
    padding-left: 30px;
  }
}
.promo {
  position: relative;
}

