    display: none;
  }
  .promo__logo {
    width: 445px;
  }
  .promo__logo-image {
    width: 445px;
    height: auto;
  }
  .promo__title {
    text-align: center;
    font-size: 28px;
  }
  .promo__desc {
    width: auto;
    margin-bottom: 50px;
    padding-right: 70px;
    padding-left: 70px;
    text-align: center;
