@media (max-width: 767px) {
  .promo__wrapper {
    padding-top: 40px;
    padding-bottom: 50px;
  }
  .promo__logo {
    width: 100%;
    max-width: 375px;
  }
  .promo__logo-image {
    width: 100%;
  }
  .promo__title {
    margin-bottom: 20px;
    font-size: 20px;
    line-height: 27px;
  }
  .promo__small-title {
    font-size: 14px;
    line-height: 20px;
  }
  .promo__desc {
    margin-bottom: 40px;
    padding: 0;
    font-size: 14px;
    line-height: 20px;
  }
  .promo__links {
    justify-content: center;
    row-gap: 25px;
  }
  .promo__link {
    font-size: 16px;
  }
}
